import React from "react";
import Card from './Card';
import'./SearchBox.css';



const SearchBox = (props) => {
	


return(
	<div className='Main'>
     
    { props.movieslist.map((user,i)=>{
	return <Card key={i} 
	id={props.movieslist[i].id} 
	title={props.movieslist[i].original_title} 
	rating={props.movieslist[i].popularity} 
	photo={props.movieslist[i].poster_path}
	viewMovieDetails={props.viewMovieDetails}
	 /> 
})  }
	</div>
	)
}

export default SearchBox 