import React from 'react';
import './Card.css';
import noimage from '.././assets/noimage.png';

const Card = (props) => {
	let photocard = null;
    if(props.photo === null )   { photocard=( <img className='photo' src={noimage} /> )} 
	else  { photocard=(<img className='photo' alt='card photo' src={`https://image.tmdb.org/t/p/w500${props.photo}`}/> ) }

return(
<div className='Card-main-container'>
     {photocard}
     <p className='Card-title'> {props.title} </p>
     <p className='Card-rating'> Rating: {props.rating}</p>
     <button className='Card-button' onClick={props.viewMovieDetails.bind(this, props.id)}> See details </button> 
</div>
);
}
	
export default Card;