import React, { Component } from 'react';
import Nav from './Components/Nav';
import InputBox from './Components/InputBox';
import Main from './Components/Main';
import SearchBox from './Components/SearchBox';
import MovieInfo from './Components/MovieInfo';
import MyCollection from './Components/MyCollection';
import { Route, Link} from 'react-router-dom';
import './App.css';

class App extends Component {
state ={
  movies:[],
  search:'',
  condition: false,
  currentMovie:null,
  favorites:[]
}

apiKey = process.env.REACT_APP_API
  
handleInput = (event)=>{
this.setState({search:event.target.value}); 
}

doSearch=(event)=> {
  event.preventDefault()
   if (this.state.search.length === 0) { return null}  else{
    fetch(`https://api.themoviedb.org/3/search/movie?api_key=${this.apiKey}&query=${this.state.search}`)
    .then(data => data.json())
    .then(data=> { this.setState({movies: [...data.results]});  this.setState({condition: true}) ;   })   
}}
 
viewMovieDetails =(id) =>{
  const filteredMovie = this.state.movies.filter(function(movie){ return movie.id == id } );
  const newCurrentMovie = filteredMovie.length > 0 ? filteredMovie[0] : null
  this.setState({currentMovie: newCurrentMovie});
}

closeMovieDetails =() =>{
this.setState({currentMovie:null})
}

addFavorites =()=>{
  const checkstate = this.state.favorites.map(user =>{return user.id})
   const checkid = this.state.currentMovie.id
   if (checkstate.includes(checkid) === true){alert('The movie is in the collection')} else{
   let joined = this.state.favorites.concat(this.state.currentMovie);
    this.setState({ favorites: joined });
}
}



render(){
  let mainPage = null;
  if(this.state.movies.length === 0){mainPage=(<div> <Main/> </div>)} 
  else {mainPage=(<div> <SearchBox viewMovieDetails={this.viewMovieDetails} movieslist={this.state.movies}/>  </div>) }
  
  let main = null;
  if (this.state.currentMovie === null ){ main =(
     <div>  <InputBox function={this.handleInput} functionclick={this.doSearch}/>  { mainPage} </div>
     )} else {main=(<div> <MovieInfo addFavorites={this.addFavorites} currentMovie={this.state.currentMovie} closeMovieDetails={this.closeMovieDetails}/> </div>) }
    
  
return (
    <div className="App">
         <Nav />
         <Route exact path='/'   render={ (props)=>(<div> {main} </div>)  }/>
        <Route path='/mycollection'    exact component={()=>(<div> <MyCollection favorites={this.state.favorites} /> </div>)}/>     
    </div>
  );
  
}
}

export default App;
