import React from 'react';

import noimage from '.././assets/noimage.png';

const Card = (props) => {
	let photocardfav = null;
    if(props.photo === null )   { photocardfav=( <img className='photo' src={noimage} /> )} 
	else  { photocardfav=(<img className='photo' alt='card photo' src={`https://image.tmdb.org/t/p/w500${props.photo}`}/> ) }

return(
<div className='Card-main-container'>
     {photocardfav}
     <p className='Card-title'> {props.title} </p>
     <p className='Card-rating'> Rating: {props.rating}</p>
      
</div>
);
}
	
export default Card;