import React from 'react';
import './InputBox.css';

const InputBox = (props) => {

	return(
<div className='InputBox-container' >
<input type='text' placeholder='add text..' onChange={props.function} className='InputBox-input' />
<button onClick={props.functionclick} className='InputBox-button'> Search </button>
</div>
);
}

export default InputBox;