import React from "react";
import './MyCollection.css';
import CardFav from './CardFav';


const MyCollection = (props) => {


return (
 <div>
 <h1 className='MyCollection-title'> My Collection </h1>
    <div className='MyCollection-container'>
     
    { props.favorites.map((user,i)=>{
	return <CardFav key={i} 
	id={props.favorites[i].id} 
	title={props.favorites[i].original_title} 
	rating={props.favorites[i].popularity} 
	photo={props.favorites[i].poster_path}
	
	 /> 
})  }
     </div>
     </div>
   );
}

export default MyCollection;
