import React from 'react';
import './Main.css';
import cover from '.././assets/cover.png';

const InputBox = (props) => {
	return(
<div>
<h1 className='Main-title'> This site is using API from MovieDB </h1>
<h1 className='Main-title'> Search movies by Title... </h1>
<h1 className='Main-example'> Example: type Spider then press Search button </h1>
<img src={cover} alt='cover' className='Main-cover'/>
</div>
);
}

export default InputBox;